import listCell from '@/components/mix-list-cell';
import uniBadge from "@/components/uni-badge.vue";
export default {
	components: {
		listCell,
		uniBadge
	},
	data() {
		return {
			USER_INFO: this.global.USER_INFO,
			USER_PHONE: this.global.USER_PHONE,
			nickName: '',
			avatarUrl: '../../static/icon/tab-my.png',
			phone: '',
			userId: '',
			companyPhoneNum: this.$base_api.PHONE_NUM,
			loginText: '登录注册',
			memberTips: '立即开通',
			isLogin: false,
			memberBtn: false,
			//待付款角标
			payCount:"0",
			payShow:false,
			//待发货角标
			sendCount:"0",
			sendShow:false,
			//待收货角标
			receiveCount:"0",
			receiveShow:false,
			//待评价角标
			commentCount:"0",
			commentShow:false,
		};
	},
	methods: {
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			if (!this.isLogin) {
				this.$openPage('Auth');
			} else { 
				uni.navigateTo({url})
			}
		},
		//跳转地址管理界面
		toAddressPage() {
			let isLogin = this.global.isLogin(this);
			if (!isLogin) {
				this.$openPage('Auth');
			} else {
				this.$openPage('address');
			}
		},
		//跳转消息界面
		toMessagePage() {
			let isLogin = this.global.isLogin(this);
			if (!isLogin) {
				this.$openPage('Auth');
			}
			uni.navigateTo({
				url: '/pages/message/message'
			})
		},
		//跳转授权页面
		toAuthPage() {
			this.$openPage('Auth');
		},

		//跳转登录页面
		toLoginPage() {
			if (this.isNotBlank(this.phone)) {
				this.loginText = '退出登录';
				this.$cache.delete(this.USER_PHONE);
				this.$cache.delete(this.global.AUTHORIZATION);
			} else {
				this.$openPage('login');
			}

		},

		//联系我们
		telUs() {
			let that = this;
			uni.makePhoneCall({
				phoneNumber: that.companyPhoneNum,
				success() {
					console.log("感谢你的来电");
				}
			})
		},

		//会员功能
		openMemberPlus() {
			if (this.memberBtn) {
				this.$msg("您已经是会员了！")
				return;
			}
			this.$openPage('Auth')
			// this.$msg("敬请期待！")
		},

		//跳转至个人信息页面
		navToSetPersonal() {
			let isLogin = this.global.isLogin(this);
			if (!isLogin) {
				this.$openPage('Auth');
				return ;
			}
			this.$minApi.uniapp_post(this.$base_api.getPersonInfo).then(res => {
				if (!res.success) {
					this.$msg(res.msg);
				}
				let param = new Object();
				param.name = 'set'
				param.query = res.data;
				this.$openPage(param)
			})
		},
			//封装查询订单请求参数
					mallOrderModel() {
						let orderModel = new Object();
						orderModel.mallId = this.$base_api.MALL_ID;
						return orderModel;
					},
					//获取订单角标
					getOrderCount(){
						//待付款角标
						this.payCount="0"
						this.payShow=false
						//待发货角标
						this.sendCount="0"
						this.sendShow=false
						//待收货角标
						this.receiveCount="0"
						this.receiveShow=false
						//待评价角标
						this.commentCount="0"
						this.commentShow=false
						let orderModel = this.mallOrderModel();
						this.$minApi.uniapp_post(this.$base_api.queryOrder, JSON.stringify(orderModel), 'json').then(res => {
							let orderListAll =[]
							orderListAll = res.data;
							console.log("AA:"+JSON.stringify(orderListAll))
							if(orderListAll.length>0){
								for(let i = 0;i<orderListAll.length;i++){
										console.log(JSON.stringify(orderListAll[i].orderStatus)+"______")
									switch(orderListAll[i].orderStatus)
									{
										case "100":
											let temp1 =parseInt(this.payCount)
											temp1++
											this.payCount=temp1.toString()
											this.payShow=true
											break;
										case '201':
										let temp2 =parseInt(this.sendCount)
										temp2++
										this.sendCount=temp2.toString()
										
											this.sendShow=true
											break;	
										case '301':
										let temp3 =parseInt(this.receiveCount)
										temp3++
										this.receiveCount=temp3.toString()
								
											this.receiveShow=true
											break;
										case '501':
										let temp4 =parseInt(this.commentCount)
										temp4++
										this.commentCount=temp4.toString()
											
											this.commentShow=true
											break;
									}
								}
							}
							})
					}
	},

	onShow() {
		let that = this;
		//判断是否登录，手机号是否绑定
		let isLogin = this.global.isLogin(this);
		let hasPhone = this.global.hasPhone(this);
		this.isLogin = isLogin;
		if (isLogin) {
			let userInfo = this.$cache.get(this.global.USER_INFO);
			this.nickName = userInfo.nickName;
			this.avatarUrl = userInfo.avatarUrl;
			this.userId = userInfo.openId;
			this.memberTips = "会员";
			this.memberBtn = true;
			this.getOrderCount()
		}
		if (hasPhone) {
			this.phone = this.$cache.get(this.global.USER_PHONE);
		}
	}
};
